import misEnum from '../../common/mis-enum.js';
export default {
	data() {
		return {
			StatusEnumMap: [],
			steps: [],
			info: {}
		}
	},
	onLoad(option) {
		global.$http.post('/car/repair/getInfo', {
			params: {
				apply_id: option.id
			},
		}).then(res => {
			if (res.status === "0") {
				this.info = res.data;
				this.StatusEnumMap = misEnum.RepairRecordEnumMap;
				this.showSteps();
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	methods: {
		showSteps() {
			//设置进度步骤
			this.steps.length = 0;
			switch (this.info.status) {
				case 0:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-red'
					});
					break;
				case 1:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: "组长审批",
						color: ''
					});
					this.steps.push({
						name: "维修出单",
						color: ''
					});
					this.steps.push({
						name: "主任审批",
						color: ''
					});
					this.steps.push({
						name: "局长审批",
						color: ''
					});
					break;
				case -1:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-1),
						color: 'text-red'
					});
					this.steps.push({
						name: "维修出单",
						color: ''
					});
					this.steps.push({
						name: "主任审批",
						color: ''
					});
					this.steps.push({
						name: "局长审批",
						color: ''
					});
					break;
				case 2:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: "维修出单",
						color: ''
					});
					this.steps.push({
						name: "主任审批",
						color: ''
					});
					this.steps.push({
						name: "局长审批",
						color: ''
					});
					break;
				case -2:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-2),
						color: 'text-red'
					});
					this.steps.push({
						name: "局长审批",
						color: ''
					});
					break;
				case 3:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: "主任审批",
						color: ''
					});
					this.steps.push({
						name: "局长审批",
						color: ''
					});
					break;
				case -3:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(4),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-3),
						color: 'text-red'
					});
					break;
				case 4:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(4),
						color: 'text-green'
					});
					this.steps.push({
						name: "局长审批",
						color: ''
					});
					break;
				case 5:
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(4),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(5),
						color: 'text-green'
					});
					break;
			}
		},
		cancleBook: function() {
			uni.showModal({
				title: '提示',
				content: '确定取消申请？',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.cancel) return;
					uni.showLoading({
						title: '提交中',
						mask: false
					});
					global.$http.post('/car/repair/cancel', {
						params: {
							apply_id: this.info.id
						},
					}).then(res => {
						uni.hideLoading();
						if (res.status === "0") {
							this.info.status = 0;
							uni.showToast({
								title: "取消成功",
								icon: 'none'
							});
							this.showSteps();
						} else {
							uni.showToast({
								title: res.msg,
								icon: 'none'
							});
						}
					}).catch(err => {
						uni.hideLoading();
						uni.showToast({
							title: err.message,
							icon: 'none'
						});
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		toEdit: function(e) {
			uni.navigateTo({
				url: '../work/saveApplyRepair?para=' + encodeURIComponent(JSON.stringify(this.info))
			});
		},
		toRepairOrder: function(e) {
			uni.navigateTo({
				url: '../work/repairOrderDetail?para=' + encodeURIComponent(JSON.stringify(this.info))
			});
		}
	}
}
